import Card from "./card/Card";
import './Articles.css';
import React, {useEffect, useState} from "react";
import {Select} from "@mui/material";

function Articles(){

    const [articles, setArticles]=useState([]);
    const [subjects, setSubjects]=useState([]);
    const [path, setPath] = useState("http://localhost:8080/api/v1/article");
    const [articleSubject, setArticleSubject] = useState(0);

    useEffect(()=>{
        setPath(JSON.parse(window.localStorage.getItem('PATH')))

        fetch(path)
            .then(res=>res.json())
            .then((result)=>{
                setArticles(result);
            })
        fetch("http://localhost:8080/api/v1/subjects")
            .then(res=>res.json())
            .then((result)=>{
                setSubjects(result);
            })
    },[])

    useEffect(()=>{
        window.localStorage.setItem('PATH', JSON.stringify(path))

        fetch(JSON.parse(window.localStorage.getItem('PATH')))
            .then(res=>res.json())
            .then((result)=>{
                setArticles(result);
            })
        fetch("http://localhost:8080/api/v1/subjects")
            .then(res=>res.json())
            .then((result)=>{
                setSubjects(result);
            })

    },[path, articleSubject])

    const handleSubject = (event) => {

        const e =document.getElementById("selectedSubject");

        setArticleSubject(e.target.value);

        if(e.target.value==0){
            setPath("http://localhost:8080/api/v1/article");
        } else {
            setPath("http://localhost:8080/api/v1/article/" + articleSubject);
        }


        fetch(path)
            .then(res=>res.json())
            .then((result)=>{
                setArticles(result);
            })
    };

    const listArticles = articles.map(article => <><Card title={article.title} text={article.article} id={article.id} /><br/></>)
    const listSubjects = subjects.map(subject => <option value={subject.id}>{subject.name}</option>)

    return(
        <>
            <select className={"selectSubject"} itemID="selectedSubject">
                <option value={0}>All subjects</option>
                {listSubjects}
            </select>
            <button onClick={handleSubject}> Select option</button>
            {listArticles}
        </>)
}

export default Articles